<template>
  <b-form class="mt-3">
    <b-row>
      <b-row>
        <h4 class="text-secondary">Author</h4>
      </b-row>
      <b-col cols="6">
        <b-form-group id="first-name" label="First Name" label-for="first-name">
          <b-form-input
            id="first-name"
            type="text"
            placeholder="First Name"
            v-model="article.author_firstname"
            ></b-form-input
          >
        </b-form-group>
      </b-col>
      <b-col cols="6">
        <b-form-group id="last-name" label="Last Name" label-for="last-name">
          <b-form-input
            id="last-name"
            type="text"
            placeholder="Last Name"
            v-model="article.author_lastname"
          ></b-form-input>
        </b-form-group>
      </b-col>
    </b-row>
    <b-row class="mt-3">
      <b-row>
        <h4 class="text-secondary">Title</h4>
      </b-row>
      <b-col cols="6">
        <b-form-group id="title" label="Title" label-for="title">
          <b-form-input
            id="title"
            type="text"
            v-model="article.title"
          ></b-form-input>
        </b-form-group>
      </b-col>
      <b-col cols="6">
        <b-form-group id="subtitle" label="Subtitle" label-for="subtitle">
          <b-form-input
            id="subtitle"
            type="subtitle"
            v-model="article.subtitle"
          ></b-form-input>
        </b-form-group>
      </b-col>
    </b-row>
    <b-row class="mt-5">
      <b-row>
        <h4 class="text-secondary">Text</h4>
      </b-row>
      <b-col cols="12">
        <b-form-textarea
          id="textarea-rows"
          placeholder="Tall textarea"
          v-model="article.text"
          rows="8"
        ></b-form-textarea>
      </b-col>
    </b-row>

    <b-row class="mt-5">
      <h4 class="text-secondary">Details</h4>
    </b-row>
    <b-row>
      <b-col cols="12">
        <div>
          <b-form-tags v-model="article.tags" no-outer-focus class="mb-2">
            <template
              v-slot="{
                tags,
                inputAttrs,
                inputHandlers,
                tagVariant,
                addTag,
                removeTag,
              }"
            >
              <b-input-group class="mb-2">
                <b-form-input
                  v-bind="inputAttrs"
                  v-on="inputHandlers"
                  placeholder="New tag - Press enter to add"
                  class="form-control"
                ></b-form-input>
                <b-input-group-append>
                  <b-button @click="addTag()" variant="primary">Add</b-button>
                </b-input-group-append>
              </b-input-group>
              <div class="d-inline-block" style="font-size: 1.5rem">
                <b-form-tag
                  v-for="tag in tags"
                  @remove="removeTag(tag)"
                  :key="tag"
                  :title="tag"
                  :variant="tagVariant"
                  class="mr-1"
                  style="background-color: green"
                  >{{ tag }}</b-form-tag
                >
              </div>
            </template>
          </b-form-tags>
        </div>
      </b-col>
    </b-row>
    <b-row>
      <b-col cols="4">
        <b-form-group id="date" label="Date" label-for="date">
          <b-form-input
            id="date"
            type="date"
            v-model="article.date"
          ></b-form-input>
        </b-form-group>
      </b-col>
      <b-col cols="4">
        <b-form-group id="link" label="Link" label-for="link">
          <b-form-input
            id="link"
            type="text"
            v-model="article.link"
          ></b-form-input>
        </b-form-group>
      </b-col>
      <b-col cols="4">
        <b-form-group id="img" label="Image" label-for="img">
          <b-form-input
            id="img"
            type="text"
            v-model="article.img"
          ></b-form-input>
        </b-form-group>
      </b-col>
    </b-row>
    <b-row class="mt-2">
      <b-form-checkbox
        id="favourite"
        v-model="article.favourite"
        name="article-favourite"
      >
        Favourite
      </b-form-checkbox>
    </b-row>
    <b-row class="mt-4">
      <b-col cols="6" class="d-flex justify-content-start">
        <b-button variant="primary" class="px-5" @click="updateArticle"
          >Update Article</b-button
        >
      </b-col>
      <b-col cols="6" class="d-flex justify-content-end">
        <b-button variant="warning" @click="triggerClose">Close</b-button>
      </b-col>
    </b-row>
  </b-form>
</template>

<script>
import axios from "axios";

export default {
  name: "CreateArticleModal",
  props: {
    articleId: Number,
  },
  data() {
    return {
      article: {},
    };
  },
  mounted() {
    this.getCusomterByID();
  },
  methods: {
    triggerClose() {
      this.$emit("closeEditModal");
    },
    getCusomterByID() {
      axios
        .get(`http://localhost:3000/articles/${this.articleId}`)
        .then((response) => {
          this.article = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    updateArticle() {
      axios
        .put(`http://localhost:3000/articles/${this.articleId}`, this.article)
        .then((response) => {
          console.log(response.data);
          this.$emit("closeEditModal");
          this.$emit("reloadDataTable");
          this.$emit("showSuccessAlert");
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>
<style scoped>

</style>