<template>
  <div>
    <b-row>
      <b-alert v-model="showSuccessAlertCreate" variant="success" dismissible>
        {{ alertMessage }}
      </b-alert>
    </b-row>
    <b-row>
      <b-alert v-model="showSuccessAlertEdit" variant="warning" dismissible>
        {{ alertMessage }}
      </b-alert>
    </b-row>
    <b-row>
      <b-alert v-model="showSuccessAlertDelete" variant="danger" dismissible>
        {{ alertMessage }}
      </b-alert>
    </b-row>
    <b-row>
      <article-overview
        :totalArticles="numberOfArticles"
        :activeArticles="activeArticles"
        @totalArticlesIsActive="setFilterTotalIsActive"
        @activeArticleIsActive="setFilterActiveIsActive"
      ></article-overview>
    </b-row>
    <b-row class="mt-3">
      <b-card>
        <b-row align-h="between">
          <b-col cols="6" class="d-flex justify-content-center">
            <h3>{{ tableHeader }}</h3>
          </b-col>
          <b-col cols="6" class="d-flex justify-content-center">
            <b-row>
              <b-col>
                <b-button
                  variant="primary"
                  id="show-btn"
                  @click="showCreateModal"
                >
                  <b-icon-plus class="text-white"></b-icon-plus>
                  <span class="h6 text-white">New Article</span>
                </b-button>
              </b-col>
            </b-row>
          </b-col>
        </b-row>
        <b-row class="mt-3">
          <b-table
            striped
            hover
            :items="items"
            :fields="fields"
            class="text-center"
          >     
            <template #cell(id)="data">
              {{
                `${data.item.id}`
              }}
            </template>       
            <template #cell(img)="data">
              <!-- <img
                fluid
                class="img-fluid"
                v-bind="mainProps"
                :src="require(`../../assets/img/blog/${data.item.img}.jpg`)"
                :alt="data.item.img"
                style="max-height: 50px; max-width: 50px;"
              > -->
              <img
                fluid
                class="img-fluid"
                v-bind="mainProps"
                :src="data.item.img"
                :alt="data.item.img"
                style="max-height: 50px; max-width: 50px;"
              >
            </template>
            <template #cell(title)="data">
              {{
                `${substringText(data.item.title, 0, 50)}`
              }}
            </template>
            <template #cell(author)="data">
              {{
                `${data.item.author_firstname} ${data.item.author_lastname}`
              }}
            </template>
            <template #cell(date)="data">
              {{
                `${data.item.date}`
              }}
            </template>
            <template #cell(status)="data">
              <i class="fa-solid fa-star" style="color: #dca818;" v-if="data.item.favourite"></i>
              <i class="fa-regular fa-star" style="color: #dca818;" v-else></i>
            </template>
            <template #cell(actions)="data">
              <b-row>
                <b-col cols="7">
                  <i class="fa-solid fa-pen-to-square" style="color: #1860dc; cursor: pointer;" @click="getRowData(data.item.id)"></i>
                </b-col>
                <b-col cols="1">
                  <i class="fa-solid fa-trash" style="color: #da1010; cursor: pointer;" @click="showDeleteModal(data.item.id)"></i>
                </b-col>
              </b-row>
            </template>
          </b-table>
        </b-row>
      </b-card>
    </b-row>

    <!-- Modal for adding new articles -->
    <b-modal
      ref="create-article-modal"
      size="xl"
      hide-footer
      title="New Article"
      style="background-color: lightgreen;"
    >
      <create-article-form
        @closeCreateModal="closeCreateModal"
        @reloadDataTable="getArticleData"
        @showSuccessAlert="showAlertCreate"
      ></create-article-form>
    </b-modal>

    <!-- Modal for updating articles -->
    <b-modal
      ref="edit-article-modal"
      size="xl"
      hide-footer
      title="Edit Article"
      style="background-color: lightyellow;"
    >
      <edit-article-form
        @closeEditModal="closeEditModal"
        @reloadDataTable="getArticleData"
        @showSuccessAlert="showAlertUpdate"
        :articleId="articleId"
      ></edit-article-form>
    </b-modal>

    <!-- Delete Article Modal -->
    <b-modal
      ref="delete-article-modal"
      size="md"
      hide-footer
      title="Confirm Deletion"
      style="background-color: lightcoral;"
    >
      <delete-article-modal
        @closeDeleteModal="closeDeleteModal"
        @reloadDataTable="getArticleData"
        @showDeleteAlert="showDeleteSuccessModal"
        :articleId="articleId"
      ></delete-article-modal>
    </b-modal>
  </div>
</template>

<script>
import axios from "axios";
import ArticleOverview from "@/components/Private/ArticleOverview.vue";
import CreateArticleForm from "@/components/Private/CreateArticleForm.vue";
import EditArticleForm from "@/components/Private/EditArticleForm.vue";
import DeleteArticleModal from "@/components/Private/DeleteArticleModal.vue";

export default {
  name: 'DataTable',
  components: {
    ArticleOverview,
    CreateArticleForm,
    EditArticleForm,
    DeleteArticleModal,
  },
  data() {
    return {
      // Note 'isActive' is left out and will not appear in the rendered table
      mainProps: { blank: true, blankColor: '#777', width: 110, height: 85 },
      fields: [
        {
          key: "id",
          label: "Id",
          sortable: false,
        },
        {
          key: "img",
          label: "Img",
          sortable: false,
        },
        {
          key: "title",
          label: "Title",
          sortable: false,
        },
        {
          key: "author",
          label: "Author",
          sortable: false,
        },
        {
          key: "status",
          label: "",
          sortable: false,
        },
        {
          key: "date",
          label: "Date",
          sortable: false,
        },
        "actions",
      ],
      items: [],
      numberOfArticles: 0,
      activeArticles: 0,
      activeArticlesData: [],
      articleId: 0,
      companySearchTerm: "",
      tableHeader: "",
      showSuccessAlertCreate: false,
      showSuccessAlertEdit: false,
      showSuccessAlertDelete: false,
      alertMessage: "",
    };
  },
  mounted() {
    this.getArticleData();
  },
  methods: {
    showCreateModal() {
      this.$refs["create-article-modal"].show();
    },
    closeCreateModal() {
      this.$refs["create-article-modal"].hide();
    },
    getArticleData() {
      axios
        .get("http://localhost:3000/articles/")
        .then((response) => {
          this.tableHeader = "Total Article";
          this.items = response.data;
          this.numberOfArticles = response.data.length;
          this.activeArticlesData = response.data.filter(
            (item) => item.favourite
          );
          this.activeArticles = this.activeArticlesData.length;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getRowData(id) {
      this.articleId = id;
      this.$refs["edit-article-modal"].show();
    },
    closeEditModal() {
      this.$refs["edit-article-modal"].hide();
    },
    setFilterTotalIsActive() {
      this.tableHeader = "Total Articles";
      this.getArticleData();
    },
    setFilterActiveIsActive() {
      this.tableHeader = "Recent Articles";
      this.items = this.activeArticlesData;
    },
    showAlertCreate() {
      this.showSuccessAlertCreate = true;
      this.alertMessage = "Article was created successfully!";
    },
    showAlertUpdate() {
      this.showSuccessAlertEdit = true;
      this.alertMessage = "Article was updated successfully";
      setInterval(() => {
      this.showSuccessAlertEdit = false;
      }, 5000);
    },
    showDeleteModal(id) {
      this.articleId = id;
      this.$refs["delete-article-modal"].show();
    },
    closeDeleteModal() {
      this.$refs["delete-article-modal"].hide();
    },
    showDeleteSuccessModal() {
      this.showSuccessAlertDelete = true;
      this.alertMessage = "Article was deleted successfully!";
    },
    substringText(string, min, max) {
      if (string.length > max) {
        return string.substring(min, max) + "...";
      } else {
        return string;
      }
    },
  },
};
</script>

<style scoped>
td,
th {
  text-align: left;
}
.action-item:hover {
  cursor: pointer;
}

@media all and (max-width: 800px){
  .card-body {
    font-size: .9rem;
  }
}

@media all and (max-width: 400px){
  .card-body {
    font-size: .6rem;
  }
  #show-btn {
    font-size: .8rem;
    padding: 4px;
  }
  h3 {
    font-size: 1.2rem;
  }
}
</style>